<template>
  <div class="volume">
    <TimeSearch @timeChange="timeChange" @search="toSearch"></TimeSearch>
    <WholeData :nums="nums"></WholeData>
    <div class="rank">
      <div class="rank-head">
        <div class="rank-title">排行榜</div>
        <MenuBar :list="menus" @change="menuChange"></MenuBar>
      </div>
      <div class="rank-list">
        <InfiniteList
          ref="list"
          v-slot="slotProps"
          api="teamManage/goodsSaleAnalyzeDetail"
          :params="listParams"
          :formatData="formatData"
        >
          <div
            class="list-item flex column"
            v-for="(item, index) in slotProps.list"
            :key="item.goods_id"
            @click="toDetail(item.goods_id, item.goods_info.goods_name)"
          >
            <div class="detail item flex ac js">
              <CustomImg
                width="1.6rem"
                height="1.6rem"
                fit="cover"
                :src="item.goods_info.original_img"
              />
              <div class="item goods-name">{{item.goods_info.goods_name}}</div>
              <div v-if="params.type === 'sale_num'" class="goods-num">
                {{item.sale_num}}
                <span>件</span>
              </div>
              <div v-if="params.type === 'sale_money'" class="goods-num">
                {{item.sale_money}}
                <span>元</span>
              </div>
              <div v-if="params.type === 'refund_num'" class="goods-num">
                {{item.refund_num}}
                <span>件</span>
              </div>
              <img v-if="index === 0 && params.type !== 'refund_num'" class="rank-icon" :src="require('@/assets/rank-one.png')" alt />
              <img v-if="index === 1 && params.type !== 'refund_num'" class="rank-icon" :src="require('@/assets/rank-two.png')" alt />
              <img
                v-if="index === 2 && params.type !== 'refund_num'"
                class="rank-icon"
                :src="require('@/assets/rank-three.png')"
                alt
              />
            </div>
            <div class="list-item-btn text-center">查看团队成员{{params.type === 'refund_num' ? '退货' : '销售'}}情况</div>
          </div>
        </InfiniteList>
      </div>
    </div>
  </div>
</template>

<script>
import TimeSearch from "@/components/TimeSearch";
import WholeData from "@/components/WholeData";
import MenuBar from "@/components/MenuBar";
import InfiniteList from "@/components/InfiniteList";

export default {
  components: { TimeSearch, WholeData, MenuBar, InfiniteList },
  data() {
    return {
      params: {
        type: "sale_num" // 排行榜类型
      }, // 参数,
      nums: [], // 整体数据
      menus: [
        { name: "商品榜", value: "sale_num" },
        { name: "成交额榜", value: "sale_money" },
        { name: "退货商品", value: "refund_num" }
      ] // 排行榜tab
    };
  },
  computed: {
    // 接口参数
    listParams() {
      const params = { ...this.params };
      delete params.timeStr;
      return params;
    }
  },
  methods: {
    // 时间段变化
    timeChange(params) {
      this.params = {
        ...this.params,
        ...params
      };
      this.$refs.list.resetData();
    },
    // 搜索
    toSearch() {
      this.$router.push("/goods/search");
    },
    // 排行榜菜单切换
    menuChange(index, value) {
      this.params = {
        ...this.params,
        type: value
      };
      this.$refs.list.resetData();
    },
    // 数据格式化
    formatData(res) {
      const { sale_num, sale_money, refund_num, list } = res;
      const { data, current_page, last_page } = list;
      const nums = [];
      nums.push(
        { name: "总销量(件)", value: sale_num },
        { name: "总成交额(元)", value: sale_money },
        { name: "总退款(件)", value: refund_num }
      );
      this.nums = nums;
      return {
        current_page,
        last_page,
        data
      };
    },
    // 商品销售情况
    toDetail(goods_id, goods_name) {
      const { time_type, start_time, end_time, timeStr, type } = this.params;
      const params = {
        time_type,
        start_time,
        end_time,
        timeStr,
        goods_id,
        goods_name: goods_name.replace(/%/g, '%25'),
        type
      };
      let str = "";
      for (let i in params) {
        str += `&${i}=${params[i]}`;
      }
      str = str.slice(1);
      this.$router.push(`/goods/detail?${str}`);
    }
  }
};
</script>

<style lang="less" scoped>
.search {
  width: 18px;
  margin-right: 13px;
}

.time-desc {
  width: 349px;
  height: 43px;
  line-height: 43px;
  padding-left: 10px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  margin: 3px auto;
  color: #777;
  font-size: 14px;
}

.rank {
  background: #fff;
}

.rank-head {
  overflow: hidden;
  background: #fff;
  padding: 0 13px 10px;
}
.rank-title {
  color: #282828;
  font-size: 16px;
  line-height: 23px;
  margin: 10px 0 15px;
}

.list-item {
  box-sizing: border-box;
  width: 349px;
  height: 114px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  border-radius: 8px;
  margin: 10px auto;
}

.list-item-btn {
  height: 34px;
  line-height: 34px;
  color: #f6d9b4;
  font-size: 12px;
  background: rgba(254, 250, 243, 1);
  border-radius: 0 0 8px 8px;
}

.detail {
  padding: 0 10px;
  position: relative;
}

.rank-icon {
  width: 25px;
  position: absolute;
  top: 0;
  right: 13px;
}

.goods-name {
  color: #252525;
  font-size: 14px;
  line-height: 20px;
  padding: 0 8px;
}

.goods-num {
  color: #252525;
  font-size: 18px;
  font-weight: 500;
  span {
    font-size: 14px;
  }
}
</style>