<template>
  <div>
    <div class="tab-box">
      <Tabs
        ref="tab"
        class="search-tabs"
        :tabs="tabs"
        swipeThreshold="6"
        lineWidth="1.41rem"
        @tabChange="tabChange"
      >
        <img
          v-if="showSearch"
          class="search"
          :src="require('@/assets/search.png')"
          alt
          @click="onSearch"
        />
      </Tabs>
    </div>
    <div class="time-desc flex">
      <div v-if="timeStr" class="item flex ac js">
        <span>数据统计日期：{{timeStr}}</span>
        <van-button
          v-if="time_type === 4 && timeStr"
          type="danger"
          size="small"
          @click="timeStr = ''"
        >修改时间</van-button>
      </div>
      <div v-else class="item flex ac js">
        <div class="item flex ac">
          <div class="time text-center" @click="showDate('start_time')">{{startTimeStr}}</div>
          <div class="line"></div>
          <div class="time text-center" @click="showDate('end_time')">{{endTimeStr}}</div>
        </div>
        <van-button type="danger" size="small" color="#E84114" @click="confirm">确定</van-button>
      </div>
    </div>
    <van-popup
      v-model="show"
      position="bottom"
      :close-on-click-overlay="false"
      safe-area-inset-bottom
    >
      <DatePicker v-if="show" :value="pickerValue" @cancel="show = false" @confirm="dateConfirm" />
    </van-popup>
  </div>
</template>

<script>
import moment from "moment";
import Vue from "vue";
import { Button, Popup } from "vant";
import Tabs from "@/components/Tabs";
import DatePicker from "@/components/DatePicker";

Vue.use(Button);
Vue.use(Popup);

const FORMATSTR = "YYYY.MM.DD";
const minDate = moment("2020-01-07 00:00:00").unix(); // 项目最早上线时间 2020.01.07

export default {
  props: {
    showSearch: {
      type: Boolean,
      default: true
    }
  },
  components: { Tabs, DatePicker },
  data() {
    return {
      tabs: [
        { name: "今日实时" },
        { name: "昨日" },
        { name: "7日" },
        { name: "30日" },
        { name: "自定义" }
      ],
      time_type: 0, // 时间段类型
      timeStr: "", // 时间描述
      start_time: "", // 自定义开始时间
      end_time: "", // 自定义结束时间
      show: false, // 时间弹窗
      property: "", // 时间标志
      pickerValue: new Date()
    };
  },
  computed: {
    startTimeStr() {
      return this.start_time.split("-").join(".");
    },
    endTimeStr() {
      return this.end_time.split("-").join(".");
    }
  },
  created() {
    const { index = 0 } = this.$route.query;
    this.tabChange(Number(index));
  },
  mounted() {
    const { index = 0 } = this.$route.query;
    this.$refs.tab.active = Number(index);
  },
  methods: {
    // 顶部tab切换
    tabChange(index) {
      this.time_type = index;
      this.p = 1;
      switch (index) {
        case 0: // 今日实时
          this.timeStr = moment().format(FORMATSTR);
          break;
        case 1: // 昨日
          this.timeStr = moment()
            .subtract(1, "days")
            .format(FORMATSTR);
          break;
        case 2: // 7日
          this.timeStr = `${moment()
            .subtract(7, "days")
            .format(FORMATSTR)}-${moment()
            .subtract(1, "days")
            .format(FORMATSTR)}`;
          break;
        case 3: // 30日
          this.timeStr = `${moment()
            .subtract(30, "days")
            .format(FORMATSTR)}-${moment()
            .subtract(1, "days")
            .format(FORMATSTR)}`;
          break;
        default:
          // 自定义默认前三个月 开始时间不能在最早上线时间之前
          if (
            moment()
              .subtract(91, "days")
              .unix() < minDate
          ) {
            this.start_time = moment(minDate * 1000).format("YYYY-MM-DD");
          } else {
            this.start_time = moment()
              .subtract(91, "days")
              .format("YYYY-MM-DD");
          }
          this.end_time = moment()
            .subtract(1, "days")
            .format("YYYY-MM-DD");
          this.timeStr = this.startTimeStr + "-" + this.endTimeStr;
      }
      this.getParams();
    },
    // 参数整合
    getParams() {
      this.$nextTick().then(() => {
        const params = {
          time_type: this.time_type, // 时间段类型
          start_time: this.start_time, // 自定义开始时间
          end_time: this.end_time, // 自定义结束时间
          timeStr: this.timeStr // 时间描述
        };
        this.$emit("timeChange", params);
      });
    },
    // 搜索
    onSearch() {
      this.$emit("search");
    },
    // 时间弹窗完成
    dateConfirm(date) {
      this[this.property] = moment(date).format("YYYY-MM-DD");
      this.show = false;
    },
    // 显示时间弹窗
    showDate(property) {
      this.property = property;
      this.pickerValue = new Date(`${this[property].split("-").join("/")}`);
      this.show = true;
    },
    // 确认时间
    confirm() {
      if (this.startTimeStr && this.endTimeStr) {
        if (moment(this.start_time).unix() > moment(this.end_time).unix()) {
          this.$toast('请选择正确的时间区间')
          return
        }
        this.timeStr = this.startTimeStr + "-" + this.endTimeStr;
        this.getParams();
      } else {
        this.$toast("请选择时间");
      }
    }
  }
};
</script>

<style lang="less" scoped>
.tab-box {
  background: #fff;
}

.search {
  width: 18px;
  margin-right: 13px;
  margin-left: 50px;
}

.search-tabs {
  padding-left: 11px;
}

.time-desc {
  box-sizing: border-box;
  width: 349px;
  height: 43px;
  line-height: 43px;
  padding: 0 10px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  margin: 5px auto;
  color: #777;
  font-size: 14px;
  border-radius: 6px;
}

.time {
  width: 105px;
  height: 30px;
  line-height: 30px;
  background: rgba(254, 250, 243, 1);
  border-radius: 2px;
  border: 1px solid rgba(246, 217, 180, 1);
  color: #282828;
  font-size: 14px;
}

.line {
  width: 8px;
  height: 2px;
  background: rgba(231, 189, 121, 1);
  margin: 0 11px;
}

